import React from "react";
import "./Header.less";
import useMyNav from "../../../router/nav";
const list = [
  {
    name: "",
    path: "/home",
  },
  {
    name: "",
    path: "/blog-list",
  },
  {
    name: "",
    path: "",
  },
  {
    name: "",
    path: "",
  },
];
function Header() {
  const { go } = useMyNav();
  const goTo = (path) => {
    if (!path) alert("");
    go(path);
  };

  return (
    <div className="header">
      <div className="title">BLOG </div>
      <div className="headerNav">
        {list.map((item) => (
          <p key={item.name} onClick={() => goTo(item.path)}>
            {item.name}
          </p>
        ))}
      </div>
    </div>
  );
}

export default Header;
